@font-face {
  font-family: 'Allstate Sans';
  src: url("./fonts/objektivmk2_w_bd-webfont.woff2") format("woff2"), url("./fonts/objektivmk2_w_bd-webfont.woff") format("woff");
  font-weight: 700;
  font-style: bold; }

@font-face {
  font-family: 'Allstate Sans';
  src: url("./fonts/objektivmk2_w_rg-webfont.woff2") format("woff2"), url("./fonts/objektivmk2_w_rg-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal; }


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

body {
  font-family: 'Allstate Sans', sans-serif;
  font-weight: 400; }


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



